import React, { useState } from 'react';
import _ from 'lodash';

const Parser = () => {
    let [gamesXML, setGamesXML] = useState()
    let [gamesMap , setGamesMap]= useState()
    let [sysMsg, setSysMsg] = useState('')

    let oParser = new DOMParser();
    let oDOM = oParser.parseFromString(gamesXML, "application/xml");
    
    let parseCollection = collection => {
        _.each(collection, gameCode => {
            for (let property in gamesMap) {
                if(property === gameCode.textContent && property !== gamesMap[property]) {
                    if (gameCode.nextElementSibling.tagName === 'updateGameCode') {
                        setSysMsg('Already converted, please download file')
                        return;
                    }
                    let newGameCode = oDOM.createElement('updateGameCode');
                    newGameCode.textContent = gamesMap[property];
                    gameCode.parentNode.insertBefore(newGameCode, gameCode.nextSibling)
                    console.log(gameCode.textContent + ' ====> ' + newGameCode.textContent);
                }
            };
        });

        return collection;
    }

    let dataParseDownload = () => {
        parseCollection(oDOM.getElementsByTagName('gameCode'));

        const XMLS = new XMLSerializer();
        let filename = "file.xml";
        let link = document.createElement('a');
        let blob = new Blob([XMLS.serializeToString(oDOM)], {type: 'text/xml'});
        link.setAttribute('href', window.URL.createObjectURL(blob));
        link.setAttribute('download', filename);
        link.dataset.downloadurl = ['text/plain', link.download, link.href].join(':');
        link.draggable = true;
        link.classList.add('dragout');
        link.click();
    }

    let handleGamesXML = (e) => {
        let file = e.target.files[0];
        let fileReader = new FileReader(); 
        fileReader.onload = e => {
            setGamesXML(fileReader.result);
            setSysMsg('XML added');
        };
        fileReader.readAsText(file);
    }

    let handleMappingFile = (e) => {
        let file = e.target.files[0];
        let fileReader = new FileReader(); 
        fileReader.onload = e => { 
            setGamesMap(JSON.parse(fileReader.result));
            setSysMsg('JSON added')
        }; 
        fileReader.readAsText(file);
    }

    return (<>
        <div className='wrapper'>
            <div>
                <h1>Games Code Mapping hello</h1>
                <h2>{sysMsg}</h2>
                <div>
                    <h4>Please choose games xml file</h4>
                    <input type='file' id='gamesXML' name='filename_xml' onChange={handleGamesXML}/>
                </div>
                <br />
                <h4>Please choose games mapping file</h4>
                <div>
                    <input type='file' id='mappingFile' name='filename_mapping' onChange={handleMappingFile}/>
                </div>
                <br />
                <hr />
                <br />
                <p><button onClick={dataParseDownload}>Convert and Download</button></p>
            </div>
        </div>
    </>
    )
}

export default Parser;