export const ADD_BUTTON = 'ADD-BUTTON';

export const action_Button = (payload) => {
    return {
        type: ADD_BUTTON,
        payload
    }
}

export const buttonReducer = (state, action) => {
    switch (action.type) {
        case ADD_BUTTON:
            state.message = action.payload;
            console.log('button clicked dispatcher');
            return state;
        default:
            console.log(action, 'dispatch yo!', action.payload);
            return state;
    }
}
